.App {
  text-align: center;
}

.container {
  max-width: 1920px;
  margin: 0 auto;
}

.posts-container {
  max-width: 1170px;
  margin: 0 auto;
  display: flex;
  margin-top: 26px;
  grid-column-gap: 130px;
  -webkit-column-gap: 130px;
          column-gap: 130px;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.posts {
  display: grid;
  grid-template-columns: 370px 370px;
  grid-column-gap: 30px;
  grid-row-gap: 30px;
  grid-template-rows: auto;
  margin-top: 34px;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.logo {
  font-weight: 700;
  font-size: 25px;
  text-transform: uppercase;
  -webkit-text-decoration: underline #c9a96e;
          text-decoration: underline #c9a96e;
}

.header-section {
  display: flex;
  justify-content: space-between;
  margin: 34px 80px;
  color: #171717;
  align-items: center;
}

.header-menu {
  display: flex;
  grid-column-gap: 20px;
  -webkit-column-gap: 20px;
          column-gap: 20px;
  font-weight: 400;
  font-size: 14px;
  align-items: center;
}

.menu-item:hover {
  color: #c9a96e;
  cursor: pointer;
}

.main-title {
  font-weight: 700;
  font-size: 40px;
  line-height: 50px;
}

.main-content-section {
  position: relative;
}

.main-content {
  color: #ffffff;
  position: absolute;
  display: flex;
  flex-direction: column;
  grid-row-gap: 15px;
  width: 50%;
  text-align: left;
  bottom: 60px;
  right: 46%;
}

.image-text {
  font-size: 14px;
  line-height: 18px;
  font-weight: 400;
}

.uppercase {
  text-transform: uppercase;
}

.main-text {
  display: flex;
  grid-column-gap: 10px;
}

.post-content {
  box-shadow: 0px 2px 6px rgb(0 0 0 / 10%);
  padding-bottom: 15px;
}

.category {
  text-transform: uppercase;
  font-weight: 400;
  font-size: 12px;
  color: #c9a96e;
  margin: 15px 0 10px 0;
  display: block;
}

.description {
  display: inline-flex;
  grid-column-gap: 20px;
  -webkit-column-gap: 20px;
          column-gap: 20px;
  margin-top: 15px;
  color: #666666;
  font-size: 15px;
  line-height: 16px;
}

.signature {
  color: #b1b1b1;
}

.highlighted-post {
  grid-column: 1 / span 2;
  text-align: start;
  box-shadow: 0px 2px 6px rgb(0 0 0 / 10%);
  padding-bottom: 30px;
}

.content {
  padding-left: 30px;
  padding-right: 126px;
}

.post-title {
  font-weight: 400;
  font-size: 20px;
  line-height: 28px;
}

.hidden {
  display: none;
}

.visible {
  display: block;
}

.paragraph {
  font-weight: 400;
  font-size: 14px;
  line-height: 24px;
  justify-self: start;
  margin-top: 20px;
}

.author-name {
  font-size: 16px;
  font-weight: 700;
  margin-top: 20px;
  display: block;
}

.sidebar {
  color: #171717;
}

.speciality {
  font-size: 14px;
  font-style: italic;
  font-weight: 400;
  line-height: 18px;
  text-align: center;
  color: #b1b1b1;
}

.author-paragraph {
  font-weight: 400;
  font-size: 14px;
  line-height: 24px;
  color: 666666;
  margin: 15px 0 15px 0;
}

.sidebar-box {
  margin-top: 20px;
  box-shadow: 0px 2px 6px rgba(0, 0, 0, 0.1);
}

.author-section {
  margin-bottom: 40px;
}

.box-content {
  padding: 0 20px 14px 20px;
}

.featured-posts {
  margin-top: 70px;
  margin-bottom: 70px;
}

.category-posts {
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  border-top: 1px solid #e5e5e5;
  padding-bottom: 15px;
  padding-top: 20px;
}

.categories {
  color: #666666;
  margin-bottom: 70px;
}

.media-tool {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  padding: 18px;
  background-color: #f8f9fa;
  color: #999999;
  grid-row-gap: 7px;
}

.social-media {
  display: grid;
  grid-template-columns: auto auto auto;
  grid-column-gap: 3px;
  grid-row-gap: 3px;
  justify-items: center;
  margin-top: 20px;
  font-size: 12px;
  line-height: 15px;
  font-weight: 400;
  margin-bottom: 70px;
}

.white-button {
  color: #171717;
  font-size: 16px;
  line-height: 21px;
  text-align: center;
}

.button {
  min-width: 270px;
  padding: 10px;
  background-color: #ffffff;
  border: 1px solid #e5e5e5;
}

.reading-button {
  color: #c9a96e;
  font-size: 12px;
  font-weight: 400;
  line-height: 16px;
  cursor: pointer;
  outline: none;
}

.tag-item {
  font-size: 14px;
  font-weight: 400;
  line-height: 18px;
  display: block;
  background-color: #f8f9fa;
  color: #666666;
  padding: 6px;
}

.tags {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  grid-column-gap: 10px;
  -webkit-column-gap: 10px;
          column-gap: 10px;
  grid-row-gap: 10px;
  row-gap: 10px;
  margin-top: 20px;
  justify-content: center;
}

.pagination {
  border: 1px solid #e5e5e5;
  display: flex;
  justify-content: space-between;
  padding: 0 20px 0 20px;
  color: #171717;
  font-size: 16px;
  line-height: 16px;
  box-sizing: border-box;
}

.older-post {
  color: #a2a2a2;
}

.pagination-button {
  background-color: transparent;
  padding: 18px 0;
  border: none;
  outline: none;
  cursor: pointer;
}

.pagination-section {
  margin-top: 40px;
  grid-column: 1 / span 2;
  border: none;
  padding: 0 150px;
}

.next-arrow {
  padding-left: 10px;
}

.previous-arrow {
  padding-right: 10px;
}

.photos-section {
  display: flex;
  grid-column-gap: 20px;
  justify-content: center;
  flex-wrap: wrap;
  grid-row-gap: 20px;
}

.instagram-photos {
  margin-top: 80px;
  padding: 0 20px;
}

.photos-paragraph {
  margin-bottom: 40px;
  font-size: 24px;
  line-height: 31px;
  font-weight: 400;
}

.footer-section {
  margin-top: 100px;
  font-size: 14px;
  line-height: 18px;
  margin-bottom: 30px;
}

.navigation-bar {
  display: flex;
  grid-column-gap: 30px;
  justify-content: center;
  margin-top: 60px;
  color: #666666;
}

.icons-list {
  display: flex;
  margin-top: 30px;
  justify-content: center;
  grid-column-gap: 20px;
  margin-bottom: 40px;
}

.copyright {
  border-top: 1px solid #e5e5e5;
  padding-top: 30px;
  color: #666666;
}

